export function convertDataItemToRuntimeItem(dataItem: any, name?: string, url?: string) {
    if (!dataItem) {
        return null;
    }

    const item = { ...dataItem, name: name || dataItem.name, children: [] };
    if (url || dataItem.url) {
        item.url = url || dataItem.url;
    }

    const childrenMap = dataItem.children;
    for (const childName in childrenMap) {
        if (!childrenMap.hasOwnProperty(childName)) continue;
        const child = childrenMap[childName];

        const childUrl = !url
            ? undefined
            : url + (url.endsWith('/') ? '' : '/') + childName + (url.endsWith('/') ? '' : '/');

        item.children.push(convertDataItemToRuntimeItem(child, childName, childUrl));
    }

    return item;
}
